var redux = require('redux');
const nameInitialState = {
    dataDragonPark : [],
    dataDetailDragonPark : [],
    dataWaterPark:[]
}
const allReducers = (state = nameInitialState, action) => {
    switch (action.type) {
        case "GET_DATA_DRAGON_PARK":
            return {...state, dataDragonPark : action.getdata}
        case "GET_DATA_DETAIL_DRAGON_PARK":
            return {...state, dataDetailDragonPark : action.getDetaildata}
        case "GET_DATA_WATER_PARK":
            return {...state, dataWaterPark : action.getdata}    
        default:
            return state
    }
}
var stores = redux.createStore(allReducers);
export default stores;
