import React, { Component, lazy, Suspense } from 'react';
import './../App.css';
import { BrowserRouter as Router, Switch, Route } from "react-router-dom";
import './../css/foodZone.css';
const Home = lazy(() => import('./Home'));
const DragonPark = lazy(() => import('./DragonPark'));
const WaterPark = lazy(() => import('./WaterPark'));
const FoodZone = lazy(() => import('./FoodZone'));
const FoodZoneDetail = lazy(() => import('./FoodZoneDetail'));
const Gallery = lazy(() => import('./Gallery'));
const DragonParkDetail = lazy(() => import('./DragonParkDetail'));
const ContactUs = lazy(() => import('./ContactUs'));
const AboutUs = lazy(() => import('./AboutUs'));
const WaterParkDetail = lazy(() => import('./WaterParkDetail'));
const News = lazy(() => import('./News'));
const LightHouse = lazy(() => import('./LightHouse'));
const Beach = lazy(() => import('./Beach'));
const OldQuarterKiosks = lazy(() => import('./OldQuarterKiosks'));
const QueenCableCars = lazy(() => import('./QueenCableCars'));
const QueenCableCarsDetail = lazy(() => import('./QueenCableCarsDetail'));
const NewsDetail = lazy(() => import('./NewsDetail'));
class App extends Component {
  render() {
    return (
      <Router>
        <div className="App">
        <Suspense fallback={<div class="loading-screen">
              <div class="loading">
                <span></span>
                <span></span>
                <span></span>
                <span></span>
              </div>
            </div>}>
          <Switch>
           <Route exact path="/" component={Home}></Route>
            <Route path="/DragonPark" component={DragonPark}></Route>
            <Route path="/WaterPark" component={WaterPark}></Route>
            <Route path="/FoodZone" component={FoodZone}></Route>
            <Route path="/FoodZoneDetail" component={FoodZoneDetail}></Route>
            <Route path="/Gallery" component={Gallery}></Route>
            <Route path="/ContactUs" component={ContactUs}></Route>
            <Route path="/AboutUs" component={AboutUs}></Route>
            <Route path="/News" component={News}></Route>
            <Route path="/LightHouse" component={LightHouse}></Route>
            <Route path="/Beach" component={Beach}></Route>
            <Route path="/QueenCableCars" component={QueenCableCars}></Route>
            <Route path="/OldQuarterKiosks" component={OldQuarterKiosks}></Route>
            <Route path="/DragonParkDetail/:id" component={DragonParkDetail}></Route>
            <Route path="/WaterParkDetail/:id" component={WaterParkDetail}></Route>
            <Route path="/QueenCableCarsDetail/:id" component={QueenCableCarsDetail}></Route>
            <Route path="/NewsDetail" component={NewsDetail}></Route>
          </Switch>
          </Suspense>
        </div>
      </Router>
    );
  }
}
export default App;